<template>
  <layout>
      <div class="page">
      <cube-scroll
      ref="scroll"
      >
        <div class="search-wrap">
          <div class="search" @click="_onSearch">
            <i class="search-i"></i>
            <span class="search-text">搜索</span>
          </div>
        </div>
		<!-- 广告 -->
        <div class="slide-wrap">
			<cube-slide :data="slideList">
				<cube-slide-item v-for="(item, index) in slideList" :key="index" @click.native="clickHandler(item, index)">
					<img :src="item.image">
				</cube-slide-item>
			</cube-slide>
        </div>
		<!-- 类目 -->
        <div class="tab-wrap">
          <ul class="tab-nav">
            <li class="tab-link" v-for="(x, index) in itemClassifyList" :key="index" @click="_openItem(x)">
              <div class="logo" :style="x.logo"></div>
              <p class="text">{{ x.classifyName }}</p>
            </li>
          </ul>
        </div>
		<!-- 今日特卖 -->
        <div class="today-sale-wrap">
          <div class="today-sale">
            <div class="sale-top">
              <i class="logo"></i>
              <span class="title">限时特价</span>
              <span class="time">10:10:10</span>
            </div>
            <ul class="sale-list">
              <li class="sale-item" v-for="(x, index) in XSQGItemList" :key="index">
                <div class="product-img">
                  <img
				  	:src="`/picture/${x.itemImg}`"
                    alt=""
                  />
                </div>
                <div class="info">
                  <div class="title">
                    <span class="name">{{x.itemName}}</span>
                    <span class="tip">{{x.goodsTexture}}</span>
                  </div>
                  <div class="price">
                    <span class="num">抢购价 ￥{{x.itemPrice}}</span>
                    <span class="count">/件</span>
                  </div>
                </div>
                <div class="btn" @click="_buy(x)">立即抢购</div>
              </li>
              <!-- <li class="sale-item">
                <div class="product-img">
                  <img
                    src="https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg"
                    alt=""
                  />
                </div>
                <div class="info">
                  <div class="title">
                    <span class="name">夏装上衣</span>
                    <span class="tip">纯棉</span>
                  </div>
                  <div class="price">
                    <span class="num">抢购价 ￥100</span>
                    <span class="count">/件</span>
                  </div>
                </div>
                <div class="btn">立即抢购</div>
              </li> -->
            </ul>
          </div>
        </div>
		<!-- 用户评价 -->
        <div class="user-evaluation-wrap">
          <div class="user-evaluation">
            <div class="evaluation-top">
              <i class="logo"></i>
            </div>
            <ul class="evaluation-list">
              <li class="evaluation-item" v-for="(x, index) in evaluationList" :key="index">
                <div class="evaluation">
                  <div class="user">
                    <div class="avatar">
                    </div>
                    <div class="info">
                      <div class="name">
                        {{x.userName}} <span class="tip">{{x.classifyName}}</span>
                      </div>
                      <div class="phone">{{x.userMobile.substr(0,3)}}****{{x.userMobile.substr(7)}}</div>
                    </div>
                  </div>
                  <div class="comment">{{x.appraiseContent}}</div>
                </div>
                <div class="time">{{x.appraiseTime}}</div>
              </li>
            </ul>
          </div>
        </div>
  		</cube-scroll>
      </div>
  </layout>
</template>

<script type="text/ecmascript-6">
import { fetchHome } from "@/api/home.js";
import { addShopCart } from "@/api/cart.js";
import { itemClassifyList } from '@/utils/generic.js';
export default {
  data() {
    return {
		slideList: [],
		itemClassifyList: [],
		evaluationList: [],
		XSQGItemList: [],
		isClick: true,
    };
  },
  computed: {
  },
  created() {
	this._fetchHome();
  },
  methods: {
    // 搜索
    _onSearch() {
      this.$router.push({ path: "/search" });
	},
	// 首页数据
	_fetchHome() {
		let params = {
			userId: 1,
		}
		fetchHome(params)
			.then(res => {
				this.evaluationList = res.appraiseList;
				this.XSQGItemList = res.XSQGItemList;
				res.XSQGItemList.forEach(item => {
					this.slideList.push(Object.assign(item, {
						url: "http://www.didichuxing.com/",
          				image: "https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg"
					}));
				});
				res.itemClassifyList.forEach(item => {
					let url = itemClassifyList.find(obj => obj.classifyId === item.classifyId)
					this.itemClassifyList.push({
						classifyId: item.classifyId,
						classifyName: item.classifyName,
						logo: {
							backgroundImage: `url(${url.logo})`
						}
					})
				});
			})
			.catch(err => {});
	},
	// 跳转类目
	_openItem(item) {
		this.$router.push({ path: '/category-select', query: { classifyId: item.classifyId }});
	},
	clickHandler(item, index) {
		console.log(item, index)
	},
	_buy: _.debounce(function(item) {
		if (!this.isClick) return;
		this.isClick = false;
		let params = {
			itemId: item.itemId,
            userId: 1,
            id: item.id,
            flag: 1
		}
		addShopCart(params).then(res => {
	    	this.toast = this.$createToast({
				txt: '已加入购物车',
				type: 'txt',
				time: 1000,
			})
			this.toast.show()
			this.isClick = true;
			item.id = res.id;
    	})
	}, 300),
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
.page {
  font-family: HiraginoSansGB-W3;
//   padding-bottom: 108px;
  height: calc(100vh - 98px);

  .search-wrap {
    background-color: #ffffff;
    padding: 12px 0 16px 0;

    .search {
      margin: 0 auto;
      width: 700px;
      height: 60px;
      line-height: 60px;
      background: rgba(242, 242, 242, 1);
      border-radius: 30px;
      text-align: center;

      .search-i {
        vertical-align: middle;
        display: inline-block;
        width: 32px;
        height: 32px;
        background-image: url('../../../static/image/common/icon_search@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }

      .search-text {
        display: inline-block;
        font-size: 24px;
        color: rgba(102, 102, 102, 1);
      }
    }
  }

  .slide-wrap {
    height: 240px;
	img {
		height: 240px;
	}
  }

  .tab-wrap {
    background-color: #ffffff;
	min-height: 360px;

    .tab-nav {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      padding-bottom: 38px;

      .tab-link {
        width: 250px;
        // height: 180px;
        text-align: center;
        padding-top: 38px;

        .logo {
          margin: 0 auto;
          width: 80px;
          height: 80px;
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }

        .text {
          margin-top: 18px;
          font-size: 24px;
          color: rgba(102, 102, 102, 1);
        }
      }
    }
  }

  .today-sale-wrap {
    margin-top: 19px;
    background-color: #ffffff;

    .today-sale {
      .sale-top {
        position: relative;
        height: 100px;
        line-height: 100px;
        text-align: right;
        padding: 0 29px;
        font-size: 24px;
        color: rgba(51, 51, 51, 1);

        .logo {
          position: absolute;
          top: 34px;
          left: 29px;
          display: inline-block;
          width: 126px;
          height: 32px;
          background-image: url('../../../static/image/home/text_jrtm@2x.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }

        .title {
          color: #333333;
        }

        .time {
          color: #F43030;
        }
      }

      .sale-list {
        padding: 0 30px;

        .sale-item {
          padding: 20px 0;
          display: flex;
          border-bottom: 0.5px solid #F2F2F2;

          .product-img {
            width: 180px;
            height: 120px;
            border-radius: 8px;

            img {
              width: 180px;
              height: 120px;
              border-radius: 8px;
            }
          }

          .info {
            flex: 1;
            padding-left: 21px;

            .title {
              margin-top: 10px;

              .name {
                font-size: 28px;
                color: #333333;
              }

              .tip {
                vertical-align: middle;
                padding: 2px 6px;
                font-size: 18px;
                background: #F89771;
                border-radius: 8px;
                color: #ffffff;
              }
            }

            .price {
              margin-top: 35px;

              .num {
                font-size: 24px;
                color: #F43030;
              }

              .count {
                font-size: 20px;
                color: #666666;
              }
            }
          }

          .btn {
            margin-top: 36px;
            width: 120px;
            height: 48px;
            line-height: 48px;
            background: rgba(255, 140, 69, 1);
            border-radius: 4px;
            font-size: 20px;
            color: rgba(255, 255, 255, 1);
            text-align: center;
          }
        }
      }
    }
  }

  .user-evaluation-wrap {
    margin-top: 19px;
    background-color: #ffffff;

    .user-evaluation {
      .evaluation-top {
        position: relative;
        width: 100%;
        height: 100px;

        .logo {
          position: absolute;
          top: 34px;
          left: 29px;
          display: inline-block;
          width: 126px;
          height: 32px;
          background-image: url('../../../static/image/home/text_yhpj@2x.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
      }

      .evaluation-list {
        padding: 0 30px;

        .evaluation-item {
          padding: 20px 0;
          display: flex;
          border-bottom: 0.5px solid #F2F2F2;

          .evaluation {
            flex: 1;

            .user {
              display: flex;

              .avatar {
                width: 60px;
                height: 60px;
                border-radius: 50%;
				background-color: #4A4C59;
              }

              .info {
                flex: 1;
                padding-left: 20px;

                .name {
                  font-size: 24px;
                  color: #333333;

                  .tip {
                    padding-left: 31px;
                    color: #FF8C45;
                    font-size: 24px;
                  }
                }

                .phone {
                  margin-top: 13px;
                  color: #666666;
                  font-size: 20px;
                }
              }
            }

            .comment {
              padding-top: 30px;
              font-size: 24px;
              color: #333333;
            }
          }

          .time {
            width: 141px;
            font-size: 24px;
            color: #999999;
          }
        }
      }
    }
  }
}

.cube-pulldown-wrapper {
  transform: translateY(-100%);
  line-height: 0;

  .pulldown-content {
    width: 100%;

    span {
      position: absolute;
      bottom: 10px;
      left: 0;
      right: 0;
      margin: auto;
      width: 200px;
      line-height: 20px;
      text-align: center;
      color: #000;
      font-size: 12px;
    }
  }
}

.surprise-page {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 33;
  line-height: 0;

  img {
    height: 100%;
    width: 100%;
  }

  .surprise-page-enter, .surprise-page-leave-to {
    transform: translateY(-100%);
  }

  .surprise-page-enter-active, .surprise-page-leave-active {
    transition: transform 0.3s;
  }
}
</style>



