import request from '@/request/http';

export function fetchHome(query) {
    return request({
        url: '/ssm/weChat/oneKeyOrder',
        method: 'get',
        params: query
    })
}
    
export function createArticle(data) {
    return request({
        url: '/ssm/article/create',
        method: 'post',
        data
    })
}

// 登录
export function wxLogin(data) {
    return request({
        url: '/ssm/wx/wxLogin',
        method: 'get',
        data
    })
}
    


